@mixin font($font_name, $file_name, $weight, $style) {
  @font-face {
    font-family: $font_name;
    font-display: swap;

    //src: url('../fonts/Inter/static/#{$file_name}.eot'); /* IE9 Compat Modes */

    src:
        //local('#{$file_name}'),
        //url('../fonts/#{$file_name}.eot?#iefix') format('embedded-opentype'), //  IE. IE6-IE8
        //url("../fonts/#{$file_name}.otf") format("opentype"),                 // ,  .
        //url('../fonts/#{$file_name}.ttf') format('truetype'),                 // Safari, Android, iOS(*)
        //url("../fonts/#{$file_name}.woff2") format("woff2"),                  // 
        url("../fonts/Inter/static/#{$file_name}.woff") format("woff");                    //   IE9+
        //url('../fonts/#{$file_name}.svg#slick') format('svg');                // Safari, iOS, Android
        font-weight: #{$weight};
        font-style: #{$style};
        // unicode-range: U+0410-044F; // 
  }
}

@import "./_font-list.scss";
